import React from 'react'
import './FeaturedMovie.css'


const FeaturedMovie = ({ item }) => {
    //console.log('FeaturedMovie -> item: ' + JSON.stringify(item))

    let firstDate = new Date(item.first_air_date);
    let genres = [];

    for (let i in item.genres) {
        genres.push(item.genres[i].name);
    }


    // TODO Limit the number of words used in the description of the movie --------------------------------
    let description = item.overview;

    if (description.length > 200) {
        description = description.substring(0, 200) + '...'
    }

    //console.log('FeaturedMovie -> Genres: ' + JSON.stringify(genres));

    return (
        <div className="featured" style={{
            backgroundSize: 'cover',
            backgroundPosition: 'center',
            backgroundImage: `${item.backdrop_path && item.backdrop_path.length > 0 ?
                `url(https://image.tmdb.org/t/p/original${item.backdrop_path})`
                : ''}`

        }}>
            {/* <div>{item.original_name}</div> */}
            <div className="featured--vertical">
                <div className="featured--horizontal">

                    <div className="featured--name">
                        {item.original_name}
                    </div>
                    <div className="featured--info">
                        <div className="featured--points">{item.vote_average} pontos</div>
                        <div className="featured--year">{firstDate.getFullYear()}</div>
                        <div className="featured--seasons">{item.number_of_seasons} temporada{item.number_of_seasons !== 1 ? 's' : ''}</div>
                        <div className="featured--description">{description}</div>
                        <div className="featured--buttons">
                            <a className='featured--buttonWatch' href={`/watch/${item.id}`}> Assistir</a>
                            <a className='featured--buttonMyList' href={`/list/add${item.id}`}>+ Minha Lista</a>
                        </div>
                        <div className="featured--genres"><strong>Gneros:</strong>{genres.join(', ')}  </div>
                    </div>

                </div>
            </div>
        </div>
    )
}

export default FeaturedMovie;